import {Button, Form, Modal} from "react-bootstrap";
import React, {useState} from "react";
import {useTranslation} from "react-i18next";

import {NotificationManager} from "../../../../soajs/urac/components";
import {MultitenantService} from "../../../../services";
import deepSet from "../../../../soajs/libs/deepSetLib";

const multitenantService = MultitenantService.getService();
const initFields = {
	"id": "",
	"name": "",
	"description": "",
	"code": "",
	"_TTL": "168"
};

export default function AddPackageModal({modalOpt, setModalOpt, handleAction}) {
	const {t} = useTranslation(["common", "soajs"]);
	const handleClose = () => setModalOpt({"show": false, item: null});
	
	const [fields, setFields] = useState(initFields);
	
	function validateForm() {
		return (
			fields.name.length > 0 &&
			fields.description.length > 0 &&
			fields.code.length === 5
		);
	}
	
	function handleFieldChange(event) {
		let value = event.target.value;
		if (event.target.hasOwnProperty("checked")) {
			value = event.target.checked;
		}
		let obj = fields;
		deepSet(obj, event.target.id, value);
		setFields({...obj});
	}
	
	async function handleSubmit() {
		try {
			fields.id = modalOpt.item.id;
			await multitenantService.addPackage(fields);
			NotificationManager.success(t("soajs:messages.packageAdd"));
			handleAction();
			handleClose();
		} catch (e) {
			NotificationManager.error(e.message);
		}
	}
	
	return (
		<Modal show={modalOpt.show} onHide={handleClose} animation={false} size="lg">
			<Modal.Header closeButton>
				<Modal.Title>{t("soajs:titles.packageAdd")}</Modal.Title>
			</Modal.Header>
			<Modal.Body>
				<Form>
					<Form.Group>
						<Form.Label>{t("soajs:fields.Code")}</Form.Label>
						<Form.Control
							placeholder="5 alphanumeric characters only"
							id="code"
							size="sm"
							value={fields.code}
							onChange={handleFieldChange}
						/>
					</Form.Group>
					<Form.Group>
						<Form.Label>{t("soajs:fields.Name")}</Form.Label>
						<Form.Control
							id="name"
							size="sm"
							value={fields.name}
							onChange={handleFieldChange}
						/>
					</Form.Group>
					<Form.Group>
						<Form.Label>{t("soajs:fields.Description")}</Form.Label>
						<Form.Control
							id="description"
							size="sm"
							as="textarea"
							rows={3}
							value={fields.description}
							onChange={handleFieldChange}
						/>
					</Form.Group>
					<Form.Group>
						<Form.Label>{t("soajs:fields.TTL")}</Form.Label>
						<Form.Control as="select" id="_TTL"
						              defaultValue={fields._TTL}
						              onChange={handleFieldChange}
						>
							<option value="6">6 hours</option>
							<option value="12">12 hours</option>
							<option value="24">24 hours</option>
							<option value="48">2 days</option>
							<option value="72">3 days</option>
							<option value="96">4 days</option>
							<option value="120">5 days</option>
							<option value="144">6 days</option>
							<option value="168">7 days</option>
						</Form.Control>
					</Form.Group>
				</Form>
			</Modal.Body>
			<Modal.Footer>
				<Button variant="warning" onClick={handleClose}>
					{t("soajs:buttons.Cancel")}
				</Button>
				<Button
					variant="success"
					onClick={handleSubmit}
					disabled={!validateForm()}
				>
					{t("soajs:buttons.Add")}
				</Button>
			</Modal.Footer>
		</Modal>
	);
}