import {Button, Modal} from "react-bootstrap";
import React from "react";
import {useTranslation} from "react-i18next";
import {NotificationManager} from "../../../../soajs/urac/components";
import {ConsoleService} from "../../../../services";

const consoleService = ConsoleService.getService();

export default function DeleteModal({modalOpt, setModalOpt, handleAction}) {
	const {t} = useTranslation(["common", "soajs"]);
	const handleClose = () => setModalOpt({"show": false, item: null});
	
	async function handleSubmit() {
		try {
			await consoleService.deleteEnvironment(modalOpt.item._id);
			NotificationManager.success(t("soajs:messages.deleteEnvironment"));
			handleAction();
			handleClose();
		} catch (e) {
			NotificationManager.error(e.message);
		}
	}
	
	return (
		<Modal show={modalOpt.show} onHide={handleClose} animation={false}>
			<Modal.Header closeButton>
				<Modal.Title>{t("soajs:buttons.Delete")}</Modal.Title>
			</Modal.Header>
			<Modal.Body>{t("soajs:messages.deleteQuestion")}</Modal.Body>
			<Modal.Footer>
				<Button variant="warning" onClick={handleClose}>
					{t("soajs:buttons.Cancel")}
				</Button>
				<Button variant="danger" onClick={handleSubmit}>
					{t("soajs:buttons.Delete")}
				</Button>
			</Modal.Footer>
		</Modal>
	);
}