import React, {useState} from "react";
import Form from "react-bootstrap/Form";
import {useHistory, useLocation, Link} from "react-router-dom";

import {LoaderButton} from "../../helpers";
import {useFormFields} from "../../libs/hooksLib";

import {NotificationManager} from '../components';
import {UracService} from "../services";
import {useTranslation} from "react-i18next";

const uracService = UracService.getService();

export default function ResetPassword() {
	const {t} = useTranslation(["common", "urac"]);
	const [fields, handleFieldChange] = useFormFields({
		newPassword: "",
		confirmNewPassword: ""
	});
	const [isLoading, setIsLoading] = useState(false);
	
	const history = useHistory();
	
	function useQuery() {
		return new URLSearchParams(useLocation().search);
	}
	
	let query = useQuery();
	const token = query.get("token");
	
	function validateForm() {
		return (
			fields.newPassword.length > 0 &&
			fields.newPassword === fields.confirmNewPassword
		);
	}
	
	async function handleSubmit(event) {
		event.preventDefault();
		
		setIsLoading(true);
		
		try {
			await uracService.resetPassword(token, fields.newPassword, fields.confirmNewPassword);
			NotificationManager.success(t("urac:msg.resetPasswordSuccess"));
			setIsLoading(false);
			history.push("/home");
		} catch (e) {
			NotificationManager.error(e.message);
			setIsLoading(false);
		}
		
	}
	
	function validatingForm() {
		return (
			<Form>
				<Form.Text className="pb-3" muted>
					{t("urac:msg.resetPasswordError")}
					@ <Link to="/forgot-password">{t("urac:Forgot Password")}</Link>.
				</Form.Text>
			</Form>
		);
	}
	
	function resetForm() {
		return (
			<Form onSubmit={handleSubmit}>
				<Form.Group controlId="newPassword">
					<Form.Label>{t("urac:New Password")}</Form.Label>
					<Form.Control
						type="password"
						value={fields.newPassword}
						onChange={handleFieldChange}
					/>
				</Form.Group>
				<Form.Group controlId="confirmNewPassword">
					<Form.Label>{t("urac:Confirm Password")}</Form.Label>
					<Form.Control
						type="password"
						onChange={handleFieldChange}
						value={fields.confirmNewPassword}
					/>
				</Form.Group>
				<LoaderButton
					block
					type="submit"
					isLoading={isLoading}
					disabled={!validateForm()}
				>
					{t("urac:button.Change")}
				</LoaderButton>
			</Form>
		);
	}
	
	return (
		<div className="URAC">
			{!token ? validatingForm() : resetForm()}
		</div>
	);
}